///
/// ADDITIONALLY
/// ===============
$maxWidth: 1280;
$paddingLeft: 20px;
$paddingRight: $paddingLeft;

%grid {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: $paddingRight;
  padding-left: $paddingLeft;
}


///
/// MAIN
/// ===============
.c-grid {
  @extend %grid;
  @if($maxWidth) {
    max-width: $maxWidth;
  }

	&-large {
		@extend %grid;
		max-width: 1480px;
	}

  &-fluid {
    @extend %grid;
    max-width: 100%;
  }
}
